.film-gallery__list {
  flex-wrap: wrap;
  list-style: none;
  display: flex;
}

.is-hidden {
  display: none;
  overflow: hidden;
}

.film-gallery__item {
  width: 100%;
  gap: 16px;
  margin-bottom: 32px;
}

.film-gallery__item:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .film-gallery__item {
    width: calc(50% - 15px);
    margin-right: 30px;
  }

  .film-gallery__item:nth-child(2n) {
    margin-right: 0;
  }
}

@media screen and (min-width: 1200px) {
  .film-gallery__item {
    width: calc(33.3333% - 20px);
    margin-right: 30px;
  }

  .film-gallery__item:nth-child(3n) {
    margin-right: 0;
  }

  .film-gallery__item:nth-last-child(-n+3) {
    margin-bottom: 0;
  }
}

.film {
  margin: 12px 0 10px;
}

.film__wrapper {
  gap: 2px;
  display: flex;
}

